import React from 'react';
export interface IPos {
  left: number;
  top: number;
}

export interface IRegion {
  left: number;
  top: number;
  width?: number;
  height?: number;
}

export interface IFullRegion {
  left: number;
  top: number;
  width: number;
  height: number;
}

export interface IOffset {
  left?: number;
  top?: number;
  right?: number;
  bottom?: number;
}

export type IDir = keyof IOffset;

export interface IOption {
  ignoreShake?: boolean; // 忽视抖动，设置最终的source的{left, top}不保留小数
  useCssBottom?: boolean;
  useCssRight?: boolean;
  useCssTransform?: boolean;
}

export interface IOverflowConfig {
  adjustX?: boolean;            // 当 source 在「横轴方向」被遮挡住的时候，是否进行自动调整
  adjustY?: boolean;            // 当 source 在「纵轴方向」被遮挡住的时候，是否进行自动调整
  alwaysByViewport?: boolean;   // 获取元素的可视矩阵时，总是根据浏览器视窗大小来裁剪区域，而不是用document的大小
  resizeWidth?: boolean;        // （慎用）当adjustX为true，并且还是放不下时，是否自动调整宽度
  resizeHeight?: boolean;       // （慎用）当adjustY为true，并且还是放不下时，是否自动调整高度
}

/**
 * b: bottom; t: top; l: left; r: right; c: center;
 * IPoints是tl、tc、tr、cl、cr、bl、bc、br除自身外的特定组合
 * eg. ['tl', 'tr'], 是将source元素的 `top - left` 与 target 元素的 `top - right` 对齐
 */
export type IAlignType = 'tl' | 'cl' | 'bl' | 'tc' | 'cc' | 'bc' | 'tr' | 'cr' | 'br';
export type IPoints = [IAlignType, IAlignType];
export type IOffsets = [number, number];

export interface IAlign extends IOption {
  points: IPoints;              // 对齐方式
  offset?: IOffsets;            // source的【x偏移量， y的偏移量】
  targetOffset?: IOffsets;      // target的【x偏移量， y的偏移量】
  overflow: IOverflowConfig;    // 当 source 被遮挡住的时候，是否进行自动调整
}

export type ICssPropertyName = keyof React.CSSProperties;

export interface IVisibleRect {
  left: number;
  top: number;
  right: number;
  bottom: number;
}

/**
 * alignRect得到的中间产物
 */
export interface IRect extends IFullRegion {
  hasAdjustedX: boolean;
  hasAdjustedY: boolean;
  hasResizedWidth: boolean;
  hasResizedHeight: boolean;
}
